.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  background-color: #40444d;
  min-height: 10hv;
  max-height: 15hv;
}

footer {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  background-color: #27324d;
  min-height: 30hv;
  text-align: center;
  position: fixed;
  bottom: 0;
  left: 0;
}

.topnav {
  overflow: hidden;
  background-color: #333;
}

.topnav a {
  float: left;
  color: #f2f2f2;
  text-align: center;

  text-decoration: none;
  font-size: 2vh;
  padding: 1em 0em;
}

.topnav a:hover {
  background-color: #ddd;
  color: black;
}

.topnav a.active {
  background-color: #4CAF50;
  color: white;
}

.topnav:focus{
  outline: 0;
  }

.Button {
  float: left;
  color: #1b7ad3;
  text-align: center;
  text-decoration: none;
  font-size: 17px;
  overflow: hidden;
  background-color: #2163b9;
  color: white;
  outline: none;
  border: none;
  width: 20vw
}

.Button:hover {
  background-color: rgb(58, 65, 155);
  color: black;
}

.ButtonActive {
  float: left;
  color: #000000;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
  background-color: rgb(52, 30, 179);
  border: none;
  outline: 0;
  width: 20vw

}

.Button::selection
{
  background: none;

}
